'use client';

import { AnimatePresence, motion, useMotionValueEvent, useScroll } from 'framer-motion';
import { Link } from 'next-view-transitions';
import { usePathname } from 'next/navigation';
import { useMemo, useState } from 'react';
import { ChevronDown } from 'lucide-react';

import { LocaleSwitcher } from '../locale-switcher';
import { Button } from '@/components/elements/button';
import { Logo } from '@/components/logo';
import { cn } from '@/lib/utils';

type PageLike = {
  id: number;
  slug: string;
  dynamic_zone?: Array<{ __component: string; heading?: string | null }>;
};

type NavItem = {
  id: number;
  URL: string;
  text: string;
  target?: string | null;
  pages?: PageLike[];
};

type Props = {
  leftNavbarItems: NavItem[];
  rightNavbarItems: { URL: string; text: string; target?: string | null }[];
  logo: any;
  locale: string;
};

const hrefForNav = (locale: string, url: string) => (url === '/' ? `/${locale}` : `/${locale}${url}`);
const hrefForPage = (locale: string, p: PageLike) => `/${locale}/${p.slug}`;

function heroForPage(p: PageLike) {
  const hero = p.dynamic_zone?.find((x) => x.__component === 'dynamic-zone.hero') as
    | { heading?: string | null; sub_heading?: string | null }
    | undefined;

  return {
    heading: hero?.heading ?? p.slug,
    sub: hero?.sub_heading ?? null,
  };
}

export const DesktopNavbar = ({ leftNavbarItems, rightNavbarItems, logo, locale }: Props) => {
  const pathname = usePathname();
  const { scrollY } = useScroll();

  const [showBackground, setShowBackground] = useState(false);
  const [openId, setOpenId] = useState<number | null>(null);
  const [hoverId, setHoverId] = useState<number | null>(null);

  useMotionValueEvent(scrollY, 'change', (v) => setShowBackground(v > 100));

  const activeId = useMemo(() => {
    for (const item of leftNavbarItems) {
      const pages = item.pages ?? [];
      if (pages.length === 0) {
        const href = hrefForNav(locale, item.URL);
        if (href === `/${locale}` ? pathname === `/${locale}` : pathname?.startsWith(href)) return item.id;
        continue;
      }
      const isChildActive = pages.some((p) => pathname?.includes(hrefForPage(locale, p)));
      if (isChildActive) return item.id;
    }
    return null;
  }, [leftNavbarItems, locale, pathname]);

  const dockIndicatorId = hoverId ?? openId ?? activeId;

  const glass = showBackground
    ? cn(
        // scroll ut√°n: black glass (Safari safe)
        'bg-black/70 supports-[backdrop-filter]:bg-black/22',
        'supports-[backdrop-filter]:backdrop-blur-2xl supports-[backdrop-filter]:backdrop-saturate-150',
        'ring-1 ring-white/10 shadow-[0_12px_40px_rgba(0,0,0,0.35)]'
      )
    : cn(
        // top: szinte transparent (nem ‚Äúfest√ºnk‚Äù r√° a hero-ra)
        'bg-transparent'
      );

  return (
    <motion.div
      initial={{ opacity: 0, y: -10, scale: 0.985 }}
      animate={{
        opacity: 1,
        y: 0,
        scale: 1,
        width: showBackground ? '86%' : '100%',
      }}
      transition={{
        opacity: { duration: 0.45, ease: [0.22, 1, 0.36, 1] },
        y: { duration: 0.45, ease: [0.22, 1, 0.36, 1] },
        scale: { duration: 0.45, ease: [0.22, 1, 0.36, 1] },
        width: { duration: 0.42, ease: [0.22, 1, 0.36, 1] },
      }}
      className={cn(
        'relative mx-auto flex w-full items-center justify-between px-4 py-3',
        'transform-gpu will-change-[transform,opacity,width]',
        'rounded-full',
        glass
      )}
    >
      {/* Bal: Logo */}
      <div className="flex items-center gap-2 z-30">
        <Logo locale={locale} image={logo?.image} company={logo?.company} />
      </div>

      {/* K√∂z√©p: Floating Dock men√º */}
      <div className="pointer-events-none absolute inset-y-0 left-1/2 z-20 flex -translate-x-1/2 items-center">
        <div
          className={cn(
            'pointer-events-auto relative flex items-center gap-1.5 rounded-full px-2 py-1',
            'max-w-[min(760px,60vw)]',
            // top √°llapot: automatikus ‚Äúinvert‚Äù ‚Üí s√∂t√©ten feh√©r, vil√°goson fekete
            !showBackground && 'mix-blend-difference text-white',
            showBackground && 'text-white mix-blend-normal',
            // extra olvashat√≥s√°g (k√ºl√∂n√∂sen k√©pes hero-n)
            'drop-shadow-[0_10px_18px_rgba(0,0,0,0.28)]'
          )}
          onMouseLeave={() => setHoverId(null)}
        >
          {/* ‚Äúdock‚Äù √ºveg alap csak scroll ut√°n (k√ºl√∂nben nem sz√≠nezz√ºk a her√≥t) */}
          {showBackground && (
            <div
              className={cn(
                'absolute inset-0 rounded-full pointer-events-none',
                'bg-white/6 ring-1 ring-white/10',
                'supports-[backdrop-filter]:backdrop-blur-xl supports-[backdrop-filter]:backdrop-saturate-150'
              )}
            />
          )}

          {/* Active / Hover pill indicator */}
          <AnimatePresence>
            {dockIndicatorId != null && (
              <motion.div
                layoutId="dock-pill"
                className={cn(
                  'absolute z-0 rounded-full',
                  showBackground ? 'bg-white/10' : 'bg-white/14' // mix-blend miatt ez ‚Äúokosan‚Äù viselkedik
                )}
                transition={{ type: 'spring', stiffness: 520, damping: 36, mass: 0.8 }}
                style={{
                  // a pill m√©ret√©t a target item adja, lent a motion span-n√°l
                }}
              />
            )}
          </AnimatePresence>

          {leftNavbarItems.map((item) => {
            const pages = item.pages ?? [];
            const hasDropdown = pages.length > 0;

            const isOpen = openId === item.id;
            const isActive = activeId === item.id;

            if (!hasDropdown) {
              const href = hrefForNav(locale, item.URL);

              return (
                <motion.span
                  key={item.id}
                  className="relative z-10"
                  onMouseEnter={() => setHoverId(item.id)}
                >
                  {(dockIndicatorId === item.id) && (
                    <motion.span
                      // ez adja a "dock-pill" m√©ret√©t + poz√≠ci√≥j√°t
                      layoutId="dock-pill"
                      className={cn(
                        'absolute inset-0 rounded-full -z-10',
                        showBackground ? 'bg-white/10' : 'bg-white/14'
                      )}
                      transition={{ type: 'spring', stiffness: 520, damping: 36, mass: 0.8 }}
                    />
                  )}

                  <Link
                    href={href}
                    target={item.target ?? undefined}
                    className={cn(
                      'inline-flex items-center justify-center rounded-full px-4 py-2 text-sm leading-[110%]',
                      'transition duration-200',
                      showBackground ? 'hover:bg-white/8' : 'hover:bg-white/10',
                      isActive && (showBackground ? 'bg-white/10' : 'bg-white/14')
                    )}
                  >
                    {item.text}
                  </Link>
                </motion.span>
              );
            }

            const isChildActive = pages.some((p) => pathname?.includes(hrefForPage(locale, p)));

            return (
              <div
                key={item.id}
                className="relative z-10"
                onMouseEnter={() => {
                  setOpenId(item.id);
                  setHoverId(item.id);
                }}
                onMouseLeave={() => setOpenId(null)}
              >
                {/* Trigger */}
                <motion.span className="relative">
                  {(dockIndicatorId === item.id) && (
                    <motion.span
                      layoutId="dock-pill"
                      className={cn(
                        'absolute inset-0 rounded-full -z-10',
                        showBackground ? 'bg-white/10' : 'bg-white/14'
                      )}
                      transition={{ type: 'spring', stiffness: 520, damping: 36, mass: 0.8 }}
                    />
                  )}

                  <button
                    type="button"
                    className={cn(
                      'inline-flex items-center justify-center rounded-full px-4 py-2 text-sm leading-[110%]',
                      'transition duration-200',
                      showBackground ? 'hover:bg-white/8' : 'hover:bg-white/10',
                      (isChildActive || isOpen) && (showBackground ? 'bg-white/10' : 'bg-white/14')
                    )}
                  >
                    <span>{item.text}</span>
                    <ChevronDown
                      className={cn(
                        'ml-1 h-4 w-4 transition duration-300',
                        isOpen && 'rotate-180',
                        showBackground ? 'text-white/80' : 'text-white/85'
                      )}
                    />
                  </button>
                </motion.span>

                {/* Dropdown (premium ‚Äúcard‚Äù) */}
                <AnimatePresence>
                  {isOpen && (
                    <motion.div
                      role="menu"
                      initial={{ opacity: 0, y: 10, scale: 0.985 }}
                      animate={{ opacity: 1, y: 0, scale: 1 }}
                      exit={{ opacity: 0, y: 8, scale: 0.99 }}
                      transition={{ duration: 0.16, ease: [0.22, 1, 0.36, 1] }}
                      className={cn(
                        'absolute left-0 top-full mt-3 z-50 min-w-[320px] overflow-hidden',
                        'rounded-2xl',
                        // mindig ‚Äúigazi‚Äù √ºveg dropdown
                        'bg-black/85 supports-[backdrop-filter]:bg-black/35',
                        'supports-[backdrop-filter]:backdrop-blur-2xl supports-[backdrop-filter]:backdrop-saturate-150',
                        'ring-1 ring-white/10 shadow-[0_26px_70px_rgba(0,0,0,0.45)]'
                      )}
                    >
                      <div className="p-2">
                        <Link
                          href={hrefForNav(locale, item.URL)}
                          className="block rounded-xl px-3 py-2 text-sm text-white/85 hover:text-white hover:bg-white/8 transition"
                          onClick={() => setOpenId(null)}
                        >
                          √ñsszes
                        </Link>

                        <div className="my-2 h-px bg-white/10" />

                        {pages.map((p) => {
                          const hero = heroForPage(p);
                          return (
                            <Link
                              key={p.id}
                              href={hrefForPage(locale, p)}
                              className="block rounded-xl px-3 py-2 hover:bg-white/8 transition"
                              onClick={() => setOpenId(null)}
                            >
                              <div className="text-sm text-white">{hero.heading}</div>
                              {hero.sub && (
                                <div className="mt-0.5 text-xs text-white/60 line-clamp-2">{hero.sub}</div>
                              )}
                            </Link>
                          );
                        })}
                      </div>
                    </motion.div>
                  )}
                </AnimatePresence>
              </div>
            );
          })}
        </div>
      </div>

      {/* Jobb: Locale + CTA */}
      <div className="flex items-center space-x-2 z-30">
        <LocaleSwitcher currentLocale={locale} />

        {rightNavbarItems.map((item, index) => (
          <Button
            key={item.text}
            variant={index === rightNavbarItems.length - 1 ? 'primary' : 'simple'}
            as={Link}
            href={hrefForNav(locale, item.URL)}
          >
            {item.text}
          </Button>
        ))}
      </div>
    </motion.div>
  );
};
